  .site-list__item {
    border-bottom: none;
    padding: 0;
  }
  .site-list__item--active {
    position: relative;
  }
  .site-list__item--active::after {
    content: "";
    position: absolute;
    top: 32px;
    right: 0;
    bottom: 0;
    left: 0;
    height: 2px;
    background: #68B738;
  }
  .site-list__item--active-index {
    position: relative;
  }
  .site-list__item--active-index::after {
    content: "";
    position: absolute;
    top: 32px;
    right: 0;
    bottom: 0;
    left: 0;
    height: 2px;
    background: #68B738;
  }
}
